.cardContainer{
    gap: 10px;
    padding: 20px;
}
.shoppingCartTitle{
    display: flex;
    justify-content: center;
    padding: 1%;
    background-color: bisque;
}
.cartCard{
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: 10px;
    
}

.cartCard i{
    color: red;
    font-size: 20px;
    height: 20vh;
}

.cartCard img{
    width: 200px;
    object-fit: cover;
}