.productsContainer {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
}

.categoryList{
    text-transform: capitalize;
    display: flex;
    justify-content: space-around;
    list-style: none;
    margin:0 0 15px 0;
    padding: 25px;
    background-color: bisque;
    font-size: larger;
    font-weight: bold;
}
.categoryList .category{
    padding: 5px 20px;
    border-radius: 10px;
    transition: 0.2s ease-in-out;
}


.dashboardCard {
  background: rgb(255, 255, 255);
    transition: 0.5s ease-in-out;
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(179, 242, 255, 1) 100%
  );
  padding: 10px;
  width: 500px;
  border-radius: 18px;
  transition: 1s;
}

.dashboardCard .img {
  height: 300px;
  overflow: hidden;
  transition: 0.5s;
}

.dashboardCard img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
  background: 100%;
}

.dashboardCard .topRow {
  justify-content: space-between;
}
.dashboardCard .topRow p {
  width: 25%;
  text-align: end;
}
.dashboardCard .topRow h2 {
  width: 70%;
}
.dashboardCard .bottomRow {
  justify-content: space-between;
}
.dashboardCard .bottomRow a {
  width: 25%;
  text-align: end;
}
.dashboardCard .bottomRow p {
  width: 70%;
}

/* hovers */

.categoryList .category:hover{
    background-color: rgba(179, 242, 255, 1);
    color: rgb(210, 134, 41);
    cursor: pointer;
    transition: 0.5s;
}

.dashboardCard:hover {
  transition: 1s linear;
  background: linear-gradient(
    180deg,
    rgba(179, 242, 255, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
  transform: translateY(-10px);
}
